import { ContactTransactionEP } from "../config/appConfig"
import { httpService } from "./http.service"


export const contactTransactionService = {
    query,
}

async function query(filterBy = {}) {
    try {
        const res = await httpService.get(ContactTransactionEP.default, filterBy)
        if (res.status === 'ok') return res.content
        else return { error: 'Cannot get contact transactions' }
    } catch (err) {
        throw err
    }
}