// Functions
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { getAllCategories } from "./redux/categoryManager/apiCalls";
import { getCredits } from "./redux/credit/apiCalls";
import { Route, Routes } from "react-router-dom";
import { ThemeProvider } from "styled-components";
import { breakpoints } from "./constant/style"

// App Routes
import Home from "./pages/Client/Home";
import Register from "./pages/Client/Register";
import Login from "./pages/Client/Login";
import Cart from "./pages/Client/Cart";
import Success from "./pages/Client/Success";
import CreditListPage from "./pages/Client/CreditListPage";
import ContactListPage from "./pages/Client/Contact/ContactListPage";
import ContactRequirementsPage from "./pages/Client/Contact/ContactRequirementsPage";
import ContactDetailsPage from "./pages/Client/Contact/ContactDetailsPage";
import ContactPage from "./pages/Client/Contact/ContactPage";
import ContactFavoritePage from "./pages/Client/Contact/ContactFavoriteFavorite";
import ContactRecommendedPage from "./pages/Client/Contact/ContactRecommendedPage";


// Admin Routes
import HomeAdmin from "./pages/admin/adminHome/AdminHome";
import UserList from './pages/admin/userList/UserList'
import User from './pages/admin/user/User'
import NewUser from './pages/admin/newUser/NewUser'
import ContactListAdmin from "./pages/admin/contactList/ContactListAdmin";
import ContactAdmin from "./pages/admin/contact/ContactAdmin";
import NewContact from "./pages/admin/newContact/NewContact";
import CreditAdmin from "./pages/admin/credit/CreditAdmin";
import ContactRequestAdmin from "./pages/admin/contactRequest/ContactRequestAdmin";

// Components
import { ProtectedRoutes } from "./components/ProtectedRoutes";
import { Loader } from "./components/Loader/Loader";
import { UserMsg } from "./components/Core/UserMsg";
import { UserBilling } from "./pages/admin/billing/UserBilling";
import { CreditCart } from "./components/Cart/CreditCart";
import { ContactCart } from "./components/Cart/ContactCart";
import DashboardContainer from "./components/admin/dashboardContainer/DashboardContainer";


const App = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    getAllCategories(dispatch)
    getCredits(dispatch)
  }, [])

  const theme = {
    breakpoints
  };

  return (
    <ThemeProvider theme={theme}>
      {/* User Routes */}
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/contacts/:category" element={<ContactListPage />} />
        <Route path="/contact/:id" element={<ContactDetailsPage />} />
        <Route path="/contact" element={<ContactPage />} >
          <Route path="/contact/requirements" element={<ContactRequirementsPage />} />
          <Route path="/contact/favorite" element={<ContactFavoritePage />} />
          <Route path="/contact/recommended" element={<ContactRecommendedPage />} />
        </Route>
        <Route path="/credits" element={<CreditListPage />} />
        <Route path="/cart" element={<Cart />} >
          <Route path="/cart/contact/:id?" element={<ContactCart />} />
          <Route path="/cart/credit" element={<CreditCart />} />
        </Route>
        <Route path="/success" element={<Success />} />
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="*" element={<Home />} />

        {/* Admin Users Only */}
        <Route element={<ProtectedRoutes />}>
          <Route element={<DashboardContainer />}>
            <Route path="/admin" element={<HomeAdmin />} />
            <Route path="/admin/users" element={<UserList />} />
            <Route path="/admin/users/billing" element={<UserBilling />} />
            <Route path="/admin/users/credits" element={<CreditAdmin />} />
            <Route path="/admin/user/:userId" element={<User />} />
            <Route path="/admin/newUser" element={<NewUser />} />
            <Route path="/admin/contacts" element={<ContactListAdmin />} />
            <Route path="/admin/contact/request" element={<ContactRequestAdmin />} />
            <Route path="/admin/contact/:contactId" element={<ContactAdmin />} />
            <Route path="/admin/new-contact" element={<NewContact />} />
            <Route path="*" element={<HomeAdmin />} />
          </Route>
        </Route>
      </Routes>
      <Loader />
      <UserMsg />
    </ThemeProvider>
  );
};

export default App;