import "./ContactRequest.css"
import { DataGrid } from "@mui/x-data-grid"
import { useEffect } from "react"
import { JsConst } from "../../../constant/JsConst"
import { DUMMY_USER_URL } from "../../../config/appConfig"
import { useState } from "react"
import { contactRequestService } from "../../../services/contactRequest.service"
import { constTranslate } from "../../../constant/translate"
import { ContactInfoModal } from "../../../components/Contact/ContactInfoModal"
import { showErrorMsg, showSuccessMsg } from "../../../services/eventBusService"
import { utilService } from "../../../services/util.service"
import { Agent } from "../../../model/agent/Agent"
import { useSelector } from "react-redux"

export default function ContactRequestAdmin() {
    const { currentUser } = useSelector(state => state.user)
    const [requests, setRequests] = useState([])
    const [modalProps, setModalProps] = useState(null)

    useEffect(() => {
        loadRequests()
    }, [])

    const loadRequests = async () => {
        try {
            const requests = await contactRequestService.query()
            setRequests(requests)
        } catch {
        }
    }

    const removeRequest = async (requestId) => {
        const res = window.confirm('Are you sure you want to delete?')
        if (!res) return
        try {
            await contactRequestService.removeContactRequest(requestId)
            setRequests((prevState) => {
                return prevState.filter((item) => item._id !== requestId)
            })
            showSuccessMsg('Request deleted successfully')
        } catch (err) {
            showErrorMsg('Cannot delete request')
        }
    }

    const columns = [
        { field: JsConst._ID, headerName: constTranslate._ID, width: 220 },
        {
            field: "agentName",
            headerName: "Agent Name",
            width: 200,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        <img className="contactListImg" src={params.row?.contact?.agent?.imgUrl?.url || DUMMY_USER_URL} alt="" />
                        {params.row?.contact?.agent?.fullname || constTranslate.UNKNOWN}
                    </div>
                );
            },
        },
        { field: JsConst.IS_APPROVED, headerName: constTranslate.IS_APPROVED, width: 220 },
        {
            field: JsConst.CREATED_AT,
            headerName: constTranslate.CREATED_AT,
            width: 220,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {utilService.getFormattedDate(new Date(params.row[JsConst.CREATED_AT]))}
                    </div>
                )
            }
        },
        {
            field: JsConst.UPDATED_AT,
            headerName: constTranslate.APPROVED_AT,
            width: 220,
            renderCell: (params) => {
                return (
                    <div className="contactListItem">
                        {params.row[JsConst.UPDATED_AT] ? utilService.getFormattedDate(new Date(params.row[JsConst.UPDATED_AT])) : constTranslate.NO_UPDATE}
                    </div>
                )
            }
        },
        {
            field: "action",
            headerName: "Action",
            width: 250,
            renderCell: (params) => {
                return (
                    <>
                        <button
                            className="contactListEdit"
                            onClick={() => setModalProps({
                                contact: params.row.contact,
                                isApproved: params.row.isApproved,
                                toApprove: true,
                                approvedIsLoading: false,
                                rejectIsLoading: false,
                                saveCb: () => isApprovedContact(params.row),
                            })}>
                            {constTranslate.CONTACT_DETAILS}
                        </button>
                        <button
                            className="btn secondary-btn"
                            onClick={() => removeRequest(params.row._id)}>
                            {constTranslate.REMOVE}
                        </button>
                    </>
                );
            },
        },
    ]

    const isApprovedContact = async (contactRequest) => {
        if (contactRequest.isApproved) {
            return showSuccessMsg('Contact already approved')
        }

        setModalProps((prevState) => ({ ...prevState, approvedIsLoading: true }))
        const res = window.confirm('Are you sure?')

        if (!res) {
            return setModalProps((prevState) => ({ ...prevState, approvedIsLoading: false }))
        }

        try {
            const res = await contactRequestService.updateContactRequest(contactRequest)
            if (res) updateRequest(res)
            showSuccessMsg('Contact approved!')
        } catch (err) {
            showErrorMsg('Cannot approve contact')
        }
        setModalProps(null)
    }

    const updateRequest = (contactRequest) => {
        const updatedRequests = requests.map(request => request._id === contactRequest._id ? { ...contactRequest } : request)
        setRequests([...updatedRequests])
    }

    return (
        <div className="contactList">
            <DataGrid
                rows={requests}
                disableSelectionOnClick
                columns={columns}
                getRowId={(row) => row._id}
                pageSize={10}
                checkboxSelection
            />
            {modalProps && <ContactInfoModal setModalProps={setModalProps} modalProps={modalProps} />}
        </div>
    )
}
